<template>
  <div style="margin: 0 auto; max-width: 1024px; padding: 0 2em;">
    <h1 style="margin-top: 2em">BASE COLORS</h1>
    <div
      v-for="c in base_colors"
      :key="'base-' + c.hex()"
      :style="{ backgroundColor: c }"
      class="colorbox">
      <div style="width: 30%">{{c.hsl().string()}}</div>
      <div style="width: 30%">{{c.hex()}}</div>
      <div style="width: 30%">{{c.rgb()}}</div>
    </div>

    <h1 style="margin-top: 4em">TINTS &amp; SHADES</h1>
    <div
      style="text-align: left"
      v-for="(info, index) in base_colors_with_tints_and_shades"
      :key="'shade-' + index">
      <h1 :style="{ color: info.base }">{{info.title}} for {{info.base.hex()}}</h1>
      <div class="shadebox">
        <div
          v-for="c in info.colors"
          :key="index + '-' + c.hex()"
          :style="{ backgroundColor: c }"
          :class="['shade', 'is-' + info.title.toLowerCase() ]">
          {{c.hex()}}
          &nbsp;
        </div>
      </div>
    </div>

    <h1 style="margin-top: 4em">COLOR WHEEL</h1>
    <div style="width: 54em; margin: 0 auto; text-align: left;">
      <!--
      num_of_colors:
      <vue-slider v-model="num_of_colors" :min="1" :max="24" :interval="1" />
      -->
      saturation_decrease:
      <vue-slider v-model="saturation_decrease" :min="0" :max="50" :interval="1" />
      lightness_increase:
      <vue-slider v-model="lightness_increase" :min="0" :max="50" :interval="1" />
      <div style="margin-top: 2em" />
      lumi_fix_ratio:
      <vue-slider v-model="lumi_fix_ratio" :min="0" :max="1" :interval="0.01" />
      lumi_target: (Avg. {{Math.round( colorwheel.reduce((acc, obj) => acc + obj.luminance, 0) / num_of_colors )}})
      <vue-slider v-model="lumi_target" :min="0" :max="255" :interval="1" />
      <div style="margin: 0.5em 0 1em 0">
        lumi_mix_method:
        <radio-button name="lumi_mix_method" label="LIGTHEN_DARKEN" :value="lumi_mix_method" @change="lumi_mix_method = $event"/>
        <radio-button name="lumi_mix_method" label="SHADE_TINT" :value="lumi_mix_method" @change="lumi_mix_method = $event"/>
        <radio-button name="lumi_mix_method" label="WHITEN_BLACKEN" :value="lumi_mix_method" @change="lumi_mix_method = $event"/>
        <radio-button name="lumi_mix_method" label="LIGHTNESS" :value="lumi_mix_method" @change="lumi_mix_method = $event"/>
      </div>
      <div style="margin: 0.5em 0 1em 0">
        lumi_calc_method:
        <radio-button name="lumi_calc_method" label="STANDARD" :value="lumi_calc_method" @change="lumi_calc_method = $event"/>
        <radio-button name="lumi_calc_method" label="PERCEIVED1" :value="lumi_calc_method" @change="lumi_calc_method = $event"/>
        <radio-button name="lumi_calc_method" label="PERCEIVED2" :value="lumi_calc_method" @change="lumi_calc_method = $event"/>
        <radio-button name="lumi_calc_method" label="PERCEIVED3" :value="lumi_calc_method" @change="lumi_calc_method = $event"/>
        <radio-button name="lumi_calc_method" label="PERCEIVED4" :value="lumi_calc_method" @change="lumi_calc_method = $event"/>
      </div>

      <div style="margin-top: 2em; color: black; font-size: 15pt;" class="colorbox">
        <div>Original</div>
        <div>Corrected luminance</div>
      </div>
      <div
        v-for="obj in colorwheel"
        :key="'wheel-' + obj.color.hex()"
        class="colorbox">
        <div
          :style="{ backgroundColor: obj.color }">
          {{obj.color.hsl().string()}} |
          {{obj.luminance}}
        </div>
        <div style="width: 20px"></div>
        <div
          :style="{ backgroundColor: obj.color_fixed }">
          {{obj.color_fixed.hsl().string()}} |
          {{obj.luminance_fixed}}
        </div>
      </div>
    </div>

    <!-- <pre style="text-align: left">{{list}}</pre> -->
    <!-- <pre style="text-align: left">{{diff}}</pre> -->
    <!-- <pre style="text-align: left">{{base_colors_with_tints_and_shades}}</pre> -->
  </div>
</template>

<script>
import VueSlider from 'vue-slider-component'
import RadioButton from '../components/RadioButton.vue'
// import 'vue-slider-component/theme/antd.css'

import Color from 'color';

/* const base_colors = [
  (new Color('#F9AE00')), // HSL: 42 100% 49%
  (new Color('#7FB800')), // HSL: 79 100% 36% (+37)
  (new Color('#084C61')), // HSL: 194 85% 21% (+115)
  (new Color('#6B0252')), // HSL: 314 96% 21% (+120)
]; */

const base_colors = [
  (new Color('#6B0252')).hue(310).saturationl(95).lightness(20), // HSL: 315 96% 21% (+120)
  (new Color('#F9AE00')).hue(40).saturationl(95).lightness(50),  // HSL: 40 100% 49%
  (new Color('#7FB800')).hue(70).saturationl(95).lightness(35),  // HSL: 75 100% 36% (+37)
  (new Color('#084C61')).hue(190).saturationl(95).lightness(20), // HSL: 190 85% 21% (+115)
];

// https://stackoverflow.com/questions/6615002/given-an-rgb-value-how-do-i-create-a-tint-or-shade

function calc_tint( color, tint_factor ) {
  // Tints: New value = current value + ((255 - current value) x tint factor)
  let rgb = color.rgb().array().map(value => {
    return value + ((255 - value) * tint_factor);
  });
  return new Color(rgb);
}

function calc_shade( color, shade_factor ) {
  // Shades: New value = current value x shade factor
  let rgb = color.rgb().array().map(value => {
    return value * ( 1 - shade_factor );
  });
  return new Color(rgb);
}

function lumi_standard(color) {
  const [R, G, B] = color.rgb().array();
  return (0.2126*R + 0.7152*G + 0.0722*B);
}

function lumi_perceived1(color) {
  const [R, G, B] = color.rgb().array();
  return (0.299*R + 0.587*G + 0.114*B);
}

function lumi_perceived2(color) {
  const [R, G, B] = color.rgb().array();
  return Math.sqrt(
    0.299 * Math.pow( R, 2 )
  + 0.587 * Math.pow( G, 2 )
  + 0.114 * Math.pow( B, 2 ) );
}

function _sRGB_to_linear( colorChannel ) {
  // Send this function a decimal sRGB gamma encoded color value
  // between 0.0 and 1.0, and it returns a linearized value.

  if ( colorChannel <= 0.04045 ) {
    return colorChannel / 12.92;
  } else {
    return Math.pow((( colorChannel + 0.055 )/1.055), 2.4);
  }
}

function _Y_to_Lstar(Y) {
  // Send this function a luminance value between 0.0 and 1.0,
  // and it returns L* which is "perceptual lightness"

  if ( Y <= ( 216 / 24389 ) ) {   // The CIE standard states 0.008856 but 216/24389 is the intent for 0.008856451679036
    return Y * (24389 / 27);  // The CIE standard states 903.3, but 24389/27 is the intent, making 903.296296296296296
  } else {
    return Math.pow(Y, 1/3) * 116 - 16;
  }
}

function lumi_perceived3(color) {
  const [R, G, B] = color.rgb().array();
  let linR = _sRGB_to_linear( R / 255 );
  let linG = _sRGB_to_linear( G / 255 );
  let linB = _sRGB_to_linear( B / 255 );

  let y = 0.2126 * linR + 0.7152 * linG + 0.0722 * linB;

  return _Y_to_Lstar( y ) * ( 255 / 100 );
}

function lumi_perceived4(color) {
  const [R, G, B] = color.rgb().array();
  let linR = _sRGB_to_linear( R / 255 );
  let linG = _sRGB_to_linear( G / 255 );
  let linB = _sRGB_to_linear( B / 255 );

  let y = Math.sqrt(
    0.299 * Math.pow( linR, 2 )
  + 0.587 * Math.pow( linG, 2 )
  + 0.114 * Math.pow( linB, 2 ) );

  return _Y_to_Lstar( y ) * ( 255 / 100 );
}

export default {
  data() {
    return {
      base_colors,
      num_of_colors: 12,
      saturation_decrease: 5,
      lightness_increase: 15,
      lumi_fix_ratio: 1.0,
      lumi_target: 180,
      lumi_mix_method: 'LIGHTNESS',
      lumi_calc_method: 'PERCEIVED2',
    }
  },
  computed: {
    base_colors_with_tints_and_shades() {
      let n = 8;
      let result = [];
      this.base_colors.forEach(rgb => {
        let color = new Color(rgb);
        let tints = [];
        for ( let i = 1; i <= n; i ++ ) {
          // tints[i] = color.lighten( i / n );
          tints[ i - 1 ] = calc_tint( color, i / n );
        }
        result.push({
          title: 'Tints',
          base: color,
          colors: tints
        })

        let shades = [];
        for ( let i = 1; i <= n; i ++ ) {
          // shades[n + i] = color.darken( i / n );
          shades[ i - 1 ] = calc_shade( color, i / n );
        }

        result.push({
          title: 'Shades',
          base: color,
          colors: shades
        })

        // for ( let i = 0; i < 2 * n; i ++ ) {
        //   shades[i] = color.lightness( ( i / ( 2 * n ) ) * 100 );
        // }
      })
      return result;
    },

    colorwheel() {
      const n = this.num_of_colors;
      // const target_lumi = 179;
      // const lumi_fix_factor = 0.8;
      const target_lumi = this.lumi_target;
      const lumi_fix_factor = this.lumi_fix_ratio;
      const method = this.lumi_mix_method;
      let lumi_func = null
      switch( this.lumi_calc_method ) {
        case 'PERCEIVED1': lumi_func = lumi_perceived1; break;
        case 'PERCEIVED2': lumi_func = lumi_perceived2; break;
        case 'PERCEIVED3': lumi_func = lumi_perceived3; break;
        case 'PERCEIVED4': lumi_func = lumi_perceived4; break;
        case 'STANDARD':   lumi_func = lumi_standard; break;
      }

      const init_color = new Color(base_colors[0]);
      let color = new Color(init_color);

      color = color
        .hsl()
        .hue(40)
        .saturationl(100 - this.saturation_decrease )
        .lightness(50 + this.lightness_increase );

      let res = [];
      for ( let i = 0; i < n; i ++ ) {
        let color_fixed = new Color(color);

        let local_target_lumi = (1 - lumi_fix_factor) * lumi_func( color )
                              + lumi_fix_factor * target_lumi;

        for ( let iter = 0; iter < 100; iter ++ ) {
          let lumi = lumi_func( color_fixed );
          let err = Math.abs( lumi - local_target_lumi );
          if ( lumi < local_target_lumi ) {
            switch ( method ) {
              case 'LIGTHEN_DARKEN': color_fixed = color_fixed.lighten( err * 0.001 ); break;
              case 'SHADE_TINT':     color_fixed = calc_tint( color_fixed, err * 0.001 ); break;
              case 'WHITEN_BLACKEN': color_fixed = color_fixed.whiten( 0.01 ); break;
              case 'LIGHTNESS':      color_fixed = color_fixed.lightness( color_fixed.lightness() + 0.25 * err * 0.5 ); break;
            }
          }
          if ( lumi > local_target_lumi ) {
            switch ( method ) {
              case 'LIGTHEN_DARKEN': color_fixed = color_fixed.darken( err * 0.001 ); break;
              case 'SHADE_TINT':     color_fixed = calc_shade( color_fixed, err * 0.001 ); break;
              case 'WHITEN_BLACKEN': color_fixed = color_fixed.blacken( 0.01 ); break;
              case 'LIGHTNESS':      color_fixed = color_fixed.lightness( color_fixed.lightness() - 0.25 * err * 0.5 ); break;
            }
          }
        }

        res[i] = {
          color: color,
          color_fixed: color_fixed,
          luminance: Math.round( lumi_func( color ) ),
          luminance_fixed: Math.round( lumi_func( color_fixed  ) ),
        };
        color = color.rotate( 360 / n )
      }
      return res;
    },
    /*
    diff() {
      let d = []
      for ( let i = 0; i < this.list.length - 1; i++ ) {
        d[i] = this.list[i] - this.list[i + 1];
      }
      return d;
    },
    list() {
      return this.base_colors.map(rgb => {
        let color = new Color(rgb);
        // console.log(color)
        // color.hsv();
        return color.hsv().color[2];
      })
    }
    */
  },
  components: {
    VueSlider,
    RadioButton,
  },
}
</script>

